<template>
    <div>
      <!-- 面包屑导航 -->
      <Breadcrumb msg1='权限管理' msg2='权限列表'></Breadcrumb>
      <el-card class="box-card">
        <!-- 表格 -->
        <el-table border :data="tableData" fit style="width: 100%">
          <el-table-column label="#" type="index" width="100"> </el-table-column>

          <el-table-column label="权限名称" prop="authName">
          </el-table-column>
          <el-table-column label="路径" prop="path">
          </el-table-column>
          <el-table-column label="权限等级">
            <template slot-scope="scoped">
              <div>
                <el-button size="mini" v-if="scoped.row.level==0" type="primary" plain>一级</el-button>
                <el-button size="mini" v-else-if="scoped.row.level==1" type="success" plain>二级</el-button>
                <el-button size="mini" v-else type="warning" plain>三级</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 -->
      </el-card>
    </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      tableData: []
    }
  },

  created () {
    this.getRights()
  },
  methods: {
    /* 获取数据列表函数 */
    async getRights () {
      const res = await this.$http.get('/rights/list')
      this.tableData = res.data.data
    }
  }
}
</script>
