<template>
  <div class="role-container">
    <Breadcrumb msg1="权限管理" msg2="角色列表"></Breadcrumb>
    <el-card class="box-card">
      <el-button type="primary" @click="addUser">添加角色</el-button>
      <!-- 表格 -->
      <el-table
        border
        :data="tableData"
        fit
        style="width: 100%;margin-top:20px;"
      >
        <el-table-column type="expand">
          <template slot-scope="scoped">
            <!-- 一级 -->
            <el-row
              class="firstRows"
              v-for="(item, index) in scoped.row.children"
              :key="index"
            >
              <!-- 一级 -->
              <el-col :span="5">
                <el-tag effect="dark" closable type="primary">
                  {{ item.authName }}
                </el-tag>
                <i style="margin:0  10px" class="el-icon-caret-right"></i>
              </el-col>
              <!-- 16等分===二级和三级 -->
              <el-col :span="19">
                <!-- 循环的二级 -->
                <el-row
                  class="secondRows"
                  style=""
                  v-for="(item2, index2) in item.children"
                  :key="index2"
                >
                  <el-col :span="5">
                    <el-tag effect="dark" closable type="success">
                      {{ item2.authName }}
                    </el-tag>

                    <i style="margin:0  10px" class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="19">
                    <el-tag
                      effect="dark"
                      style="margin:  10px;"
                      v-for="(item3, index3) in item2.children"
                      :key="index3"
                      closable
                      type="warning"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index" width="100"> </el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
        <el-table-column label="操作">
          <template  slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"
              >编辑</el-button
            >
            <el-button size="mini" type="danger" icon="el-icon-delete"
              >删除</el-button
            >
            <el-button @click="setRights(scope.row)" size="mini" type="warning" icon="el-icon-setting"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 表格 -->
    </el-card>
    <!-- 树形 -->
    <el-dialog title="提示" :visible.sync="dialogVisibleTree" width="30%">
      <el-tree
        :data="data"
        ref="tree"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="defaultId"
        :props="defaultProps"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleTree = false">取 消</el-button>
        <el-button type="primary" @click="submitbtn()"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!-- 添加角色 -->
    <el-dialog title="添加角色" :visible.sync="adduser" width="50%" class="demo-ruleForm">
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="80px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="ruleForm.roleName" ></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="ruleForm.roleDesc" ></el-input>
        </el-form-item>
        <el-form-item class="submit-box">
          <el-button @click="adduser = false" >取消</el-button>
          <el-button type="primary" @click="addForm('ruleForm')">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      tableData: [],
      dialogVisibleTree: false,
      data: [],
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      adduser: false,
      ruleForm: {
        roleName: '',
        roleDesc: ''
      },
      rules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 2, max: 8, message: '角色名称长度在2~8个字符之间', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' }
        ]
      },
      rolesId: '',
      defaultId: []
    }
  },

  created () {
    this.getRoles()
  },
  methods: {
    /* 获取数据列表函数 */
    async getRoles () {
      const res = await this.$http.get('/roles')
      this.tableData = res.data.data
    },

    /* 获取数据列表函数 */
    async getRights () {
      const res = await this.$http.get('/rights/tree')
      this.data = res.data.data
    },

    /* 点击分配权限按钮 */
    setRights (rows) {
      this.defaultId = []
      /* 获取权限 */
      this.getRights()
      this.digui(rows, this.defaultId)
      this.dialogVisibleTree = true
    },
    // 获取最里层id递归函数
    digui (rows, arr) {
      if (!rows.children) {
        arr.push(rows.id)
        return
      }
      rows.children.forEach(item => {
        this.digui(item, arr)
      })
    },
    // 分配权限的确认按钮
    async submitbtn () {
      // console.log(this.$refs.tree.getCheckedKeys())
      // console.log(this.$refs.tree.getHalfCheckedKeys())
      const res = await this.$http.post(`/roles/${this.rolesId}/rights`, {
        rids: [...this.$refs.tree.getCheckedKeys(), ...this.$refs.tree.getHalfCheckedKeys()].join(',')
      })
      if (res.data.meta.status === 200) {
        this.$message({
          message: '分配权限成功',
          type: 'success'
        })
        this.rolesId = ''
        this.dialogVisibleTree = false
        this.getRoles()
      }
    },

    // 添加用户
    addUser () {
      this.adduser = true
    },

    // 添加用户确认按钮
    addForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const res = await this.$http.post('/roles', this.ruleForm)
          if (res.data.meta.status === 201) {
            this.$message({
              message: '创建成功',
              type: 'success'
            })
            this.getRoles()
            this.adduser = false
          }
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.role-container {
  .firstRows {
    &:nth-child(1) {
      border-top: 1px solid #ccc;
    }
    border: 1px solid #ccc;
    border-top: 0;
    display: flex;
    align-items: center;
    text-align: center;
    .secondRows {
      padding: 10px 0;
      display: flex;
      align-items: center;
      border: 1px solid #ccc;
      border-bottom: 0;
      &:nth-child(1) {
        border-top: 0;
      }
      .el-col-19 {
        text-align: left;
      }
    }
  }
}
.submit-box{
    margin-top: 50px;
    text-align: right;
  }
</style>
